* {
  box-sizing: border-box;
}

body {
  background-image: url(./Images/GlobalBackgroundImage.jpeg);
  background-repeat: repeat;
  background-size: contain;
  background-position: center top;
}

/* If you want to land in a higher position, increase the number of pixels in both padding-top and margin-top. */
a.anchor {
  display:block;
  padding-top: 100px;
  margin-top: -100px;
}






.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
